import React from 'react';
import styles from './menu.module.css';


const Menu = props => {
    return (
        <div className={styles.menu}>
            <div className={styles.div}>
            <a className={styles.link} to="/contact">Contact</a>
            <a className={styles.link} to="/contact">Bio</a>
            <a className={styles.link} to="/contact">Work</a>
            </div>
        </div>
    )
}

export default Menu;